import React, { useState, useEffect } from 'react';
import FoodLensDemo from './components/FoodLensDemo';
import LandingPage from './components/LandingPage';
import SimpleSignupPage from './components/SimpleSignupPage';
import MacroDashboard from './components/MacroDashboard';
import UserProfile from './components/UserProfile';
import MealPlan from './components/MealPlan';
import AdminDashboard from './components/AdminDashboard';
import AdminLogin from './components/AdminLogin';
import { secureStorage, hasAdminAccess } from './utils/security';

function App() {
  const [currentView, setCurrentView] = useState('landing'); // 'landing', 'signup', 'profile', 'app', 'dashboard', 'mealplan', 'admin'
  const [user, setUser] = useState(null);
  const [isLoading, setIsLoading] = useState(true);
  const [showAdminLogin, setShowAdminLogin] = useState(false);

  useEffect(() => {
    // Check if user has already signed up using secure storage
    const userData = secureStorage.getItem('caltrax-user');
    const hasSignedUp = secureStorage.getItem('caltrax-signed-up') === true;
    
    if (userData && hasSignedUp) {
      setUser(userData);
      
      // Check if trial has expired
      if (userData.plan === 'trial' && userData.trialEnds) {
        const trialEnd = new Date(userData.trialEnds);
        const now = new Date();
        if (now > trialEnd) {
          // Trial expired, redirect to signup
          setCurrentView('signup');
        } else {
          setCurrentView('app');
        }
      } else {
        setCurrentView('app');
      }
    } else {
      setCurrentView('landing');
    }
    
    setIsLoading(false);
  }, []);

  const handleGetStarted = () => {
    setCurrentView('signup');
  };

  const handleSignup = (userData) => {
    setUser(userData);
    // Check if user has completed profile
    if (userData.profile) {
      setCurrentView('app');
    } else {
      setCurrentView('profile');
    }
  };

  const handleProfileComplete = (updatedUser) => {
    setUser(updatedUser);
    setCurrentView('app');
  };

  const handleLogout = () => {
    secureStorage.removeItem('caltrax-signed-up');
    secureStorage.removeItem('caltrax-user');
    setUser(null);
    setCurrentView('landing');
  };

  const handleShowAdmin = () => {
    // Check admin access before allowing navigation
    if (hasAdminAccess()) {
      setCurrentView('admin');
    } else {
      // Show admin login modal
      setShowAdminLogin(true);
    }
  };

  const handleAdminLoginSuccess = (adminSession) => {
    setShowAdminLogin(false);
    setCurrentView('admin');
  };

  const handleAdminLoginCancel = () => {
    setShowAdminLogin(false);
  };

  const handleShowDashboard = () => {
    setCurrentView('dashboard');
  };

  const handleBackToApp = () => {
    setCurrentView('app');
  };

  const handleAddFood = () => {
    setCurrentView('app');
  };

  const handleShowMealPlan = () => {
    setCurrentView('mealplan');
  };

  const handleBackToDashboard = () => {
    setCurrentView('dashboard');
  };

  if (isLoading) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-zinc-950 via-zinc-900 to-black flex items-center justify-center">
        <div className="text-white text-xl">Loading...</div>
      </div>
    );
  }

  return (
    <div className="App">
      {currentView === 'landing' && <LandingPage onGetStarted={handleGetStarted} />}
      {currentView === 'signup' && <SimpleSignupPage onSignup={handleSignup} />}
      {currentView === 'profile' && (
        <UserProfile 
          onComplete={handleProfileComplete}
          user={user}
        />
      )}
      {currentView === 'app' && (
        <FoodLensDemo 
          onLogout={handleLogout} 
          onShowDashboard={handleShowDashboard}
          onShowMealPlan={handleShowMealPlan}
          user={user}
        />
      )}
      {currentView === 'dashboard' && (
        <MacroDashboard 
          onBack={handleBackToApp}
          onAddFood={handleAddFood}
          onShowMealPlan={handleShowMealPlan}
        />
      )}
      {currentView === 'mealplan' && (
        <MealPlan 
          onBack={handleBackToDashboard}
          user={user}
        />
      )}
      {currentView === 'admin' && hasAdminAccess() && (
        <AdminDashboard 
          onBack={() => setCurrentView('app')}
        />
      )}
      
      {/* Admin Login Modal */}
      {showAdminLogin && (
        <AdminLogin
          onSuccess={handleAdminLoginSuccess}
          onCancel={handleAdminLoginCancel}
        />
      )}
    </div>
  );
}

export default App;
